___CSS_LOADER_EXPORT___.push([module.id, `.menu-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

#menu {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
    grid-gap: 2rem;
    width: 100%;
    max-width: 48rem;

    background: var(--background);
    border-radius: 1rem;
}

.menu-item > img {
    width: 13rem;
}

.menu-item {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    cursor: pointer;

    margin: 1.5rem;
    background: var(--background-menu-items);
    border-radius: 1rem;
}

.menu-item > p {
    margin: 1rem;
}
